<template>
    <div class="parametros">          
          <v-col cols="12" >         

            <!-- ADICIONANDO SELECT MULTIPLO -->
            <v-select
              v-on:change="addParam($event)"
              :items="params"
              hint="A lista de Parmetros possui apenas os Parmetros utilizados no clculo do indicador selecionado"
              chips
              deletable-chips
              solo
              multiple
              label="Selecione os parmetros que deseja flexibilizar"
              item-text="name"
              eager
              item-value="name"
              no-data-text="Sem parmetros para selecionar"
              persistent-hint
            />
          </v-col>
     
    </div>
    
</template>

<script>
export default {
    props: ['params'],
    data: () => ({
        //parmetros para seleo
      autoUpdate: true,
     
    }),
  methods: {
    addParam: function (param) {
      this.$emit('addParam', param)
    },
  }
    
}
</script>

<style scoped>
    .msg{
        color:#CCCCCC;
        font-style: italic;
        font-size: 0.9em;
    }
</style>>
